import React, { useEffect } from "react";
import { Grid } from "@material-ui/core";
import { shortNumber } from "../../../utills/helpers";
import { useDispatch, useSelector } from "react-redux";
import { requestTotalPostViews } from "../../../redux/actions";
import { IRootState } from "../../../interfaces";
import { WidgetLoader } from "./../Common/Loader";

export const PostViewsCard = () => {
  const dispatch = useDispatch();
  const { isLoading, hasError, data } = useSelector(
    (state: IRootState) => state.postViewsReducer
  );
  useEffect(() => {
    dispatch(requestTotalPostViews());
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <Grid container>
        <Grid item sm={4}>
          {isLoading ? (
            <WidgetLoader />
          ) : hasError ? (
            <p className={"no-margin"}>Error</p>
          ) : (
            <p className="card-count color-1">
              {shortNumber(data.overall || 0, false)} 
            </p>
          )}
          <p className="number-text">Overall</p>
        </Grid>
        <Grid item sm={4}>
          {isLoading ? (
            <WidgetLoader />
          ) : hasError ? (
            <p className={"no-margin"}>Error</p>
          ) : (
            <p className="card-count color-2">
              {shortNumber(data.lastThrityDays || 0, false)} 
            </p>
          )}
           <p className="number-text">Last 30 Days</p>
        </Grid>
        <Grid item sm={4}>
          {isLoading ? (
            <WidgetLoader />
          ) : hasError ? (
            <p className={"no-margin"}>Error</p>
          ) : (
            <p className="card-count color-3">
              {shortNumber(data.lastSevenDays || 0, false)} 
            </p>
          )}
           <p className="number-text">Last 7 Days</p>
        </Grid>
      </Grid>
    </>
  );
};
