import React, { useState, useEffect } from "react";
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button, TextField } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import AppButton from "../Common/AppButton";
import { IPaymentBoxModal } from "../../../interfaces/PaymentBox";
import { requestCreatePayout, requestPayoutMethods } from "../../../redux/actions";
import { CreatePayoutValidation } from "../../validations";
import { getValidationErrors, shortNumber } from "../../../utills/helpers";
import { IRootState } from "../../../interfaces";
import Loader from "../Common/Loader";
import AppSelect from "../Common/AppSelect";

export const PaymentBox = ({
    open,
    availableBalance,
    handleClose
}: IPaymentBoxModal) => {
    const { isLoading } = useSelector(
        (state: IRootState) => state.payoutReducer
    );
    const { list, isLoading: isPaymentMethodLoading } = useSelector(
        (state: IRootState) => state.paymentMethodsReducer
    );
    const [payoutMethods, setPayoutMethods] = useState<{ label: string, value: string }[]>([])
    const [inputs, setInputs] = useState<any>({
        mode: 'bank_account',
        amount: '',
        payoutTo: ""
    })
    const [errors, setErrors] = useState({
        mode: '',
        amount: ''
    })
    // To reset component states after modal close
    useEffect(() => {
        setInputs({
            mode: 'bank_account',
            amount: '',
            payoutTo: ""
        });
        setErrors({
            mode: '',
            amount: ''
        })
        if (open) {
            dispatch(requestPayoutMethods());
        }
        // eslint-disable-next-line
    }, [open]);
    useEffect(() => {
        if (list && list.length) {
            let temp = list.map((item: any, index: number) => ({
                label: item.account_type === "vpa" ? `UPI | ${item.vpa.address}` : `${item.bank_account.bank_name} | ${item.bank_account.account_number}`,
                value: item.id
            }));
            setPayoutMethods(temp);
            setInputs({
                mode: 'bank_account',
                amount: '',
                payoutTo: temp[0].value
            });
        }
    }, [list])
    const handleChange = (event: any) => {
        const { target: { name, value } } = event;
        setInputs({
            ...inputs,
            [name]: value
        });
        setErrors({
            ...errors,
            [name]: ''
        })
    }
    const dispatch = useDispatch();
    const createPayout = () => {
        console.log(inputs, 'inputs');
        try {
            if (inputs && inputs.mode === 'vpa' && Number(inputs.amount) > 100000) {
                setErrors({ ...errors, amount: 'Amount should be less than 100000 in order to transfer through UPI.' });
            }
            CreatePayoutValidation.validateSync(({ ...inputs, amount: inputs.amount ? Number(inputs.amount) : 0, availableBalance }), {
                abortEarly: false,
            });
            dispatch(requestCreatePayout(inputs))
        } catch (error) {
            setErrors({ ...getValidationErrors(error) });
        }
    }

    return <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
    >

        <form className={"form-container"}>
            <DialogTitle id="form-dialog-title">
                Send payment
      </DialogTitle>
            <DialogContent>
                {isPaymentMethodLoading ? <Loader /> :
                    <>
                        <DialogContentText>
                            Enter amount and method you want to transfer into your account
                </DialogContentText>
                        <>
                            <AppSelect
                                label={"Choose Method*"}
                                options={payoutMethods}
                                name={"payoutTo"}
                                value={inputs.payoutTo}
                                onChange={handleChange}
                            />
                            {/* <RadioGroup
                                row
                                name="payoutTo"
                                value={inputs.payoutTo}
                                onChange={handleChange}
                            >
                                {list && list.length ?
                                    list.map((item: any, index: number) => {
                                        let value = item.id
                                        let label = item.account_type === "vpa" ? item.vpa.address : item.bank_account.account_number
                                        return (<FormControlLabel
                                            value={value}
                                            control={<Radio value={value} color="primary" />}
                                            label={label}
                                            labelPlacement="end"
                                        />
                                        )
                                    }) : ""}
                            </RadioGroup> */}
                            <br />
                            <TextField
                                label="Enter amount"
                                type="text"
                                fullWidth
                                variant="outlined"
                                onChange={handleChange}
                                name="amount"
                                value={inputs.amount}
                                error={!!errors.amount}
                                helperText={errors.amount}
                            />
                            <div className={"available-balance"}><b>Available Balance:</b> &#8377;{shortNumber(availableBalance)}</div>
                        </>
                    </>
                }

            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="primary">
                    Cancel
                </Button>
                <AppButton
                    onClick={createPayout}
                    color="primary"
                    text={"Send payment"}
                    isLoading={isLoading}
                />
            </DialogActions>
        </form>
    </Dialog>
}