import React, { useEffect } from "react";
import { Grid } from "@material-ui/core";
import { shortNumber } from "../../../utills/helpers";
import { useDispatch, useSelector } from "react-redux";
import { IRootState } from "../../../interfaces";
import { requestTotalEarnings } from "../../../redux/actions";
import { WidgetLoader } from "../Common/Loader";

export const TotalEarningCard = () => {
  const dispatch = useDispatch();
  const { isLoading, hasError, data } = useSelector(
    (state: IRootState) => state.earningsReducer
  );
  const { overall = 0, lastThrityDays = 0, lastSevenDays = 0 } = data || {};
  useEffect(() => {
    dispatch(requestTotalEarnings());
    // eslint-disable-next-line
  }, []);
  const renderValues = (amount: number) => (
    isLoading ? (
      <WidgetLoader />
    ) : hasError ? (
      <p className={"no-margin"}>Error</p>
    ) : <>&#8377;{shortNumber(amount)} <br /></>
  )
  return (
    <>
      <Grid container>
        <Grid item sm={4}>
        <p className="card-count color-1">{renderValues(overall || 0)}</p>
          <p className="number-text">Overall</p>
        </Grid>
        <Grid item sm={4}>
        <p className="card-count color-2">{renderValues(lastThrityDays || 0)}</p>
          <p className="number-text">Last 30 Days</p>
        </Grid>
        <Grid item sm={4}>
        <p className="card-count color-3">{renderValues(lastSevenDays || 0)}</p>
          <p className="number-text">Last 7 Days</p>
        </Grid>
      </Grid>
    </>
  );
};
