import React, { useEffect } from "react";
import { Grid } from "@material-ui/core";
import { shortNumber } from "../../../utills/helpers";
import { useDispatch, useSelector } from "react-redux";
import { requestTotalPosts } from "../../../redux/actions";
import { IRootState } from "../../../interfaces";
import { WidgetLoader } from "../Common/Loader";

export const TotalPostsCard = () => {
  const dispatch = useDispatch();
  const { isLoading, hasError, data } = useSelector(
    (state: IRootState) => state.postsAnalyticsReducer
  );
  const { 
    totalPost = 0,
    totalActivePost = 0,
    totalPendingPost = 0,
    totalRejectedPost = 0
  } = data || {};
  useEffect(() => {
    dispatch(requestTotalPosts());
    // eslint-disable-next-line
  }, []);
  const renderValues = (value: number) => (
    isLoading ? (
      <WidgetLoader />
    ) : hasError ? (
      <p className={"no-margin"}>Error</p>
    ) : <>{shortNumber(value, false)} <br /></>
  )
  return (
    <>
      <Grid container spacing={1}>
        <Grid item sm={3}>
          <p className="card-count color-1">{renderValues(totalPost)}</p>
          <p className="number-text">Total</p>
        </Grid>
        <Grid item sm={3}>
          <p className="card-count color-2">{renderValues(totalActivePost)}</p>
          <p className="number-text">Active</p>
        </Grid>
        <Grid item sm={3}>
          <p className="card-count color-3">{renderValues(totalPendingPost)}</p>
          <p className="number-text">Pending</p>
        </Grid>
        <Grid item sm={3}>
          <p className="card-count color-3">{renderValues(totalRejectedPost)}</p>
          <p className="number-text">Rejected</p>
        </Grid>
      </Grid>
    </>
  );
};
