import React from "react";
import {
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormHelperText,
} from "@material-ui/core";
import { IAppSelectProps, IAppSelectOption } from "../../../interfaces";

const AppSelect = ({
  options = [],
  label,
  disabled,
  helperText,
  ...restProps
}: IAppSelectProps) => (
  <FormControl className={"form-input"} variant="outlined" fullWidth>
    <InputLabel id="demo-simple-select-outlined-label">{label}</InputLabel>
    <Select disabled={disabled || !options.length} label={label} {...restProps}>
      {options.map((option: IAppSelectOption, index: number) => (
        <MenuItem value={option.value || option.label} key={index}>
          {option.label}
        </MenuItem>
      ))}
    </Select>
    {helperText ? <FormHelperText error>{helperText}</FormHelperText> : null}
  </FormControl>
);

export default AppSelect;
