import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Grid, Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import { IRootState } from "../../../interfaces";
import { requestTotalAvailableBalance, toggleModals } from "../../../redux/actions";
import { shortNumber } from "../../../utills/helpers";
import { WidgetLoader } from "../Common/Loader";
import { PaymentBox } from "./PaymentBox";
// import AddPayoutMethod from "../MyProfile/PayoutMethods/AddPayoutMethod";

export const AvailableBalanceCard = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const [open, setOpen] = React.useState(false)
  const { availableBalanceReducer: { isLoading, hasError, data }, openModal } = useSelector(
    (state: IRootState) => ({
      availableBalanceReducer: state.availableBalanceReducer,
      openModal: state.modalReducer.showPaymentBoxModal,
      openAddModal: state.modalReducer.showAddPaymentMethodModal,
    }),
  );

  const { availableBalance = 0, payoutEnabled = false } = data || {};

  useEffect(() => {
    dispatch(requestTotalAvailableBalance());
    // console.log('Loaded service worker!');

    // window.addEventListener('push', (ev:any) => {
    //   const data = ev.data.json();
    //   console.log('Got push', data);
    //   let a:any = window
    //   a?.registration.showNotification(data.title, {
    //     body: 'Hello, World!',
    //     icon: 'http://mongoosejs.com/docs/images/mongoose5_62x30_transparent.png'
    //   });
    // });
    // eslint-disable-next-line
  }, []);

  const handleClick = () => {
    // const isMobile = (/iphone|ipod|android|ie|blackberry|fennec/).test
    (navigator.userAgent.toLowerCase());
    // if (isMobile) {
    setOpen(!open)
    // }
  }
  return (
    <>
      <Grid container
        justify="space-between">

        <Grid item>
          {isLoading ? (
            <WidgetLoader />
          ) : hasError ? (
            <p className={"no-margin"}>Error</p>
          ) : <p className="card-count color-1">&#8377;{shortNumber(availableBalance)}
              </p>}

          <p className="number-text">Available</p>
        </Grid>
        <Grid item>
          {isLoading ? (
            <WidgetLoader />
          )
            :
            payoutEnabled ? <Button
              variant="contained"
              className="btn-primary"
              onClick={() =>
                dispatch(toggleModals({ showPaymentBoxModal: true }))
              }
              disabled={availableBalance < 1}
              color="primary"
            >Get Paid</Button>
              :
              // <Tooltip
              //   title="Please add payout methods in order to transfer amount into your account"
              // >
              <Button
                variant="contained"
                className="btn-primary"
                onClick={handleClick}
              >Get Paid
                </Button>
            // </Tooltip>
          }</Grid>
      </Grid>
      <PaymentBox open={openModal}
        availableBalance={availableBalance ? Number(availableBalance) : 0}
        handleClose={() =>
          dispatch(toggleModals({ showPaymentBoxModal: false }))
        } />
      {/* <Grid item sm={6}>
        <AppButton
        text={"Get Paid"}
        color="primary"
      /></Grid> */}
      <Dialog
        open={open}
        onClose={handleClick}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            You haven't added any payout method yet. Please add payout method in order to withdraw money.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {
            // dispatch(toggleModals({ showAddPaymentMethodModal: true }))
            setOpen(false)
            history.push('/profile?addPayout=true')
          }} color="primary" autoFocus>
            Add Payout Method
          </Button>
          <Button onClick={handleClick} color="primary" autoFocus>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};
